
window.addEventListener('DOMContentLoaded', () => {
    menu = document.querySelector('.header__menu'),
    hamburger = document.querySelector('.hamburger');

    hamburger.addEventListener('click', () => {
        hamburger.classList.toggle('hamburger--active');
        menu.classList.toggle('header__menu--active');
    });
});

$(window).on('load', function () {
    console.log('load');
    $().fancybox();
    $(".lazy").lazyload();

    $('a[href^="#"]:not(a.popup):not(a.more-btn)').on('click', function() {
        let href = $(this).attr('href');
        $('html, body').animate({
            scrollTop: $(href).offset().top
        });
        return false;
    }); 


    $('.pile-slider').slick({
        lazyLoad: 'ondemand',
        dots: true,
        infinite: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        adaptiveHeight: true
    });

    $('.portfolio-slider').slick({
        lazyLoad: 'ondemand',
        dots: false,
        infinite: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        fade: true,
        adaptiveHeight: true
    });

    $('.object__photos').slick({
        lazyLoad: 'ondemand',
        dots: false,
        infinite: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        adaptiveHeight: true
    });

    $('.media__photos').slick({
        lazyLoad: 'ondemand',
        dots: false,
        infinite: true,
        slidesToShow: 4,
        slidesToScroll: 4,
        responsive: [
            {
                breakpoint: 1300,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                }
            },
            {
                breakpoint: 992,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                }
            },
            {
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                }
            }
    ]
    });
    $('.media__videos').slick({
        lazyLoad: 'ondemand',
        dots: false,
        infinite: false,
        slidesToShow: 3,
        slidesToScroll: 4,
        responsive: [
            {
                breakpoint: 1300,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                }
            },
            {
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                }
            }
    ]
    });
    $('.company-slider').slick({
        lazyLoad: 'ondemand',
        dots: false,
        infinite: true,
        slidesToShow: 3,
        slidesToScroll: 3,
        responsive: [
            {
                breakpoint: 1300,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                }
            },
            {
                breakpoint: 992,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                }
            },
            {
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                }
            }
        ]
    });


    if($(window).width() < 992){
    $(".steps__item").click(function() { 
        $(this).toggleClass('steps__item--active');
        if($(this).find('.steps__item-content').text() !== ''){//Если текст есть
            $(this).find('.steps__item-content').slideToggle();// плавно спускаем вниз текст ответа на вопрос
        }
    });
    }else{        
            //Табы
        $('ul.steps__list').on('click', 'li:not(.steps__item--active)', function() {
            $(this)
                .addClass('steps__item--active').siblings().removeClass('steps__item--active')
                .closest('div.steps__content').find('div.steps__item-content').removeClass('steps__item-content--active').eq($(this).index()).addClass('steps__item-content--active');
        });
    }

    // $('input[name="tel"]').mask("+7 (999) 999-99-99");

let quizItems = document.querySelectorAll('.quiz-screen');
let quizBtns = document.querySelectorAll('.quiz-btn');

for(let i = 0; i < quizBtns.length; i++) {
    quizBtns[i].addEventListener('click', function (e) {
        e.preventDefault();
        quizItems[i].classList.remove('quiz-screen--active');
        quizItems[i+1].classList.add('quiz-screen--active');
    });
}


    $('form').on('submit', function(e) {
        e.preventDefault();
        let form = $(this);
        $.ajax({
            type: 'POST',
            url: '../send.php',
            data: $(this).serialize(),
            success: function (data) {
                data = JSON.parse(data);
                console.log(data.result);
                if(data.result === 'success') {
                    if(form.hasClass('quiz')){
                        form.find('#quiz-screen-4').removeClass('quiz-screen--active');
                        form.find('#quiz-screen-finish').fadeIn();
                    } else {
                        form.fadeOut();
                        $('#successful').fadeIn();
                        $('.fancybox-bg, .fancybox-inner, .fancybox-outer, .fancybox-stage').fadeOut();
                    }
                }
                form.find("input").val("");
                $('form').trigger('reset');
            }
        });
        return false;
    });

});